.intro{
    background-color: white;
    display: flex;
    .left{

        flex: 0.5;
        overflow-y: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
       
        
        .imgContainer{
            border-radius: 100%;
            border: solid pink;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 800px;
            height: 800px;
            overflow: hidden;
            
            

            img{
                object-fit: cover;
                height: 100vh;
                width: 100vw;
                
            }
        }
    }
    .right{
        flex: 0.5;
        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            h1{
                font-size: 60px;
                margin: 10px 0;
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;
            }
            
        }
        a{
            position: absolute;
            bottom: 10px;
            .icon{
                font-size: 25px;
                color: black;
            }
        }
        
    }
}